@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
.tag{
    margin-left: 7%;
    /* border: 1px solid; */
    max-height: 26px;
    cursor: pointer;
    border-color: #a07ee1 !important;
    color: #a07ee1;
    background: #dbcafa;
    font-weight: 700;
}

.resolved{
    border-color: #5ed795 !important;
    color: #5ed795;
    background: #d1f5e1;
}

.pending{
    border-color: rgb(235 58 101) !important;
    color: rgb(235 58 101);
    background: #fda7a7;
}
